<template>
  <div class="container">
    <IndexHeader>微信二维码生成</IndexHeader>
    <QrEditor :qrText="qrText">
      <template #contentTitle>输入内容，开始制作二维码</template>
      <template #tips>功能描述：生成微信公众号二维码。</template>
      <template #help>
        <p><span>1. 有效期：</span>本页面是【微信静态二维码】，生成后永久有效。</p>
        <p>
          <span>2. 公众号二维码原理</span
          >：根据您输入的微信号，系统将自动访问腾讯提供的接口，查询出微信公众号对应的二维码，并为您生成新的样式。用户扫描后即可查看、关注公众号。
        </p>
        <p><span>注意：由于腾讯未开放个人微信号接口，所以本功能无法生成个人微信二维码。</span></p>
      </template>
      <template #content>
        <a-input-search
          v-model:value="wxid"
          :maxlength="50"
          :loading="loading"
          allowClear
          placeholder="输入 微信号 或 原始ID（只支持公众号）"
          enter-button="Enter 确认"
          @search="getWxQrText"
          @pressEnter="getWxQrText"
          style="width: 600px"
        />
        <a-alert type="info">
          <template #description>
            <p>使用方法：</p>
            <p>
              打开微信公众号 -> 点击公众号右上角图标 -> 点击公众号简介，复制“微信号”。
              <a-popover placement="bottomLeft">
                <span class="help">图片教程</span>
                <template #content>
                  <img src="@/assets/wechat.png" alt="如何获取微信号" />
                </template>
              </a-popover>
            </p>
            <p>注：本功能只支持微信公众号。 个人微信请使用【 二维码解码 】功能</p>
          </template>
        </a-alert>
      </template>
    </QrEditor>
  </div>
</template>

<script>
import IndexHeader from '@/components/IndexHeader.vue';
import QrEditor from '@/components/QrEditor/QrEditor.vue';
import { reactive, toRefs } from 'vue';
import { useHead } from '@vueuse/head';
import { message } from 'ant-design-vue';
import request from '@/utils/request';

export default {
  name: 'Wechat',
  components: {
    IndexHeader,
    QrEditor
  },
  setup() {
    useHead({
      title: '微信二维码生成 - 兔子二维码',
      meta: [
        { name: 'keywords', content: '二维码,微信公众号' },
        {
          name: 'description',
          content: '生成微信公众号二维码'
        }
      ]
    });

    let view = reactive({
      wxid: '',
      qrText: '',
      loading: false
    });

    async function getWxQrText() {
      if (view.loading || !view.wxid) return;
      if (!/^[a-zA-Z]{1}[-_a-zA-Z0-9]{4,20}$/.test(view.wxid)) return message.warning('微信号格式错误!');

      try {
        view.loading = true;
        let { decoded } = await request.getWx(view.wxid);
        view.qrText = decoded[0].text;
      } catch (error) {
        view.qrText = '';
      } finally {
        setTimeout(() => (view.loading = false), 1500);
      }
    }

    return {
      ...toRefs(view),
      getWxQrText
    };
  }
};
</script>

